import { NgModule } from '@angular/core';

import { AppSharedModule } from '../shared/app-shared.module';
import { QuestionaireRoutingModule } from './questionaire.routing';

// Import your library
import { MaterializeModule } from "angular2-materialize";
import { ImageUploadModule } from 'ng2-imageupload';

//  angular4-drag-drop /** npm i angular4-drag-drop --save **/
import { DragDropDirectiveModule} from "angular4-drag-drop";

import { QuestionaireComponent } from './questionaire.component';
import { FormComponent } from './form/form.component';
import { ListingComponent } from './listing/listing.component';
import { UserQuestionsListingComponent } from './user-questions/listing/listing.component';
import { UserQuestionsFormComponent } from './user-questions/form/form.component';
import { UserQuestionsViewComponent } from './user-questions/view/view.component';
import { NextComponent } from './next/next.component';

import { QuestionaireService } from "./questionaire.service";
import { UserQuestionsService } from "./user-questions/user-questions.service";

@NgModule({
  imports: [
    AppSharedModule,
    QuestionaireRoutingModule,
    MaterializeModule,
    ImageUploadModule,
    DragDropDirectiveModule
  ],
  providers: [QuestionaireService,UserQuestionsService],
  declarations: [QuestionaireComponent, FormComponent, ListingComponent, UserQuestionsListingComponent, UserQuestionsFormComponent, UserQuestionsViewComponent, NextComponent]
})
export class QuestionaireModule { }



