import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { QuestionaireComponent } from './questionaire.component';
import { FormComponent } from './form/form.component';
import { ListingComponent } from './listing/listing.component';
import { UserQuestionsListingComponent } from './user-questions/listing/listing.component';
import { UserQuestionsFormComponent } from './user-questions/form/form.component';
import { UserQuestionsViewComponent } from './user-questions/view/view.component';
import { NextComponent } from './next/next.component';


const routes: Routes = [
  {
    path: '',
    component: QuestionaireComponent,
    data: { title: 'Survey' },
    children: [
      { path: '', component: ListingComponent, data: { title: '' } },
      { path: 'new', component: FormComponent, data: { title: 'New' } },
      { path: ':id/edit', component: FormComponent, data: { title: 'Edit' } },
      { path: ':__id/next', component: NextComponent, data: { title: 'Add question for Survey' } },
      { path: 'user-questions', component: UserQuestionsListingComponent, data: { title: 'Questions' } },
      { path: 'user-questions/new', component: UserQuestionsFormComponent, data: { title: 'Add questions' } },
      { path: 'user-questions/:id/view', component: UserQuestionsViewComponent, data: { title: 'View User questions' } },
      { path: 'user-questions/:id/edit', component: UserQuestionsFormComponent, data: { title: 'Edit User questions' } },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})

export class QuestionaireRoutingModule { }



