import { Component, OnInit, ViewChild } from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import { DomSanitizer } from '@angular/platform-browser';
import { NgxIntlTelInputModule } from 'ngx-intl-tel-input';
import {MaterializeDirective} from "angular2-materialize";

import { UserQuestionsService } from "../user-questions.service";
// import { NotificationsService } from '../../../shared/notifications.service';

import { environment } from '../../../../environments/environment';
import { BaseForm } from '../../../shared/base-form';

@Component({
  selector: 'app-form',
  providers: [],
  templateUrl: './form.component.html',
  styleUrls: ['./form.component.css']
})
export class UserQuestionsFormComponent extends BaseForm  implements OnInit {
  
  question: any ;
  language: any ;
  url: string ;
  category: any = [] ;
  primary: any = false ;
  dependencyQuestions = [] ;
  showdependency: any = false ;
  elementTypes: any = [];
  genders: any = [];
  isAnswerOption: boolean = false ;
  // answers: any = [];

  public input = [];
  public minimum = 1;
  public maximum = 54;

  constructor(
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private klassService: UserQuestionsService,
    // private notificationsService: NotificationsService,
    private sanitizer: DomSanitizer
  ) { super(); }

  ngOnInit() {
    
    this.language = 'en';
    this.getCategoryItems();
    this.item.categoryId = "";
    this.item.answerKeys = [];
    this.item.elementType = "Text" ;
    this.item.gender = "Both" ;
    this.elementTypes = ["Text", "Radio"] ;
    this.genders = ["Male","Female","Both"] ;
    
    // subscribe to router event
    this.activatedRoute.params.subscribe((params: Params) => {
        this.itemID = params['id'];
        if(this.itemID){
          this.isEditable = true;
          this.getItem();
        }
      });

      this.interventionsList()
  }

  getItem() {
    this.busy = this.klassService.getItem(this.itemID, this.language, 'userquestions/edit').then((res) => {
      console.log (res);
      if(!res)
        return true;
      this.item = res.data;
      
      console.log(res.data.answerKeys.length);

      if(this.item.elementType == 'Radio'|| this.item.elementType == 'Scroll Bar'){
          if(res.data.answerKeys.length > 0){
              this.item.answerKeys = res.data.answerKeys;
          }
      }

      this.isAnswerOption = this.item.elementType == 'Radio' || this.item.elementType == 'Scroll Bar' ? true : false ;

    });
  }

  getCategoryItems() {
    this.busy = this.klassService.getItems(this.language, 'categories').then((res) => {
      if(!res)
        return true;
      this.category = res.data;
    });
  }

  getCetagoryQuestion (categoryId) {

    this.dependencyQuestions = [];
    this.busy = this.klassService.getItems(this.language, 'categoryquestions/'+categoryId).then((res) => {
      if(!res)
        return true;

        if(res.data.length > 0){
          this.dependencyQuestions = res.data ; 
          if(res.data.length > 0) {
            this.item.dependency = res.data[0]._id ;
          }
        }
        
    });

  }

  checkSelectedQuestion (qId) {
    
      if(qId != ''){
        this.showdependency = true ;
        this.item.dependencyAnswer = "Yes" ;
      }else{
        this.showdependency = false ;
      }

  }

  createItem() {

    this.busy = this.klassService.addItem(this.item, this.language, 'userquestions')
    .then((res) => {
      if(!res)
        return true;

      this.item = res;
      this.router.navigate(['/questionaire/user-questions']);
      // this.notificationsService.notify('success', this.constantMessages.questionCreated);
    });
    
  }

   updateItem() {
   //console.log(this.answers) ;
   console.log(this.item) ;
       //this.item.answerKeys = this.answers ;
      this.busy = this.klassService.updateItem(this.itemID, this.item, this.language, 'userquestions').then((res) => {
        if(!res)
          return true;
        //this.form.form.markAsPristine();
        // this.notificationsService.notify('success', this.constantMessages.questionUpdated);
      });
    }

  addOption(optionLenght){
 
   let answerOption_id = Math.floor((1 + Math.random()) * 0x10000)
                        .toString(16)
                        .substring(1);
  console.log( answerOption_id );
    let newotpion = {
                      answer_id: answerOption_id,
                      displayText:"",
                      answerValue:"",
                      answerScore:"",
                      displayOrder:""} ;
    this.item.answerKeys.push(newotpion) ;
  }
  removeOption(){

    if( this.item.answerKeys.length == 2 ){
        // this.notificationsService.notify('error', "Can't remove less than two.");
        return false ;
    } else {
      this.item.answerKeys.splice(-1,1)
    }
  }

  interventionsList(){

    for(var i=this.minimum;i<=this.maximum;i++){
      this.input.push(i);
    }
    return this.input
  }

}



