import { Component, OnInit } from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';

import { Listing } from '../../../shared/listing';

import { UserQuestionsService } from "../user-questions.service";
import { environment } from '../../../../environments/environment';

@Component({
    selector: 'app-view',
    templateUrl: './view.component.html',
    providers: [],
    styleUrls: ['./view.component.css']
})

export class UserQuestionsViewComponent implements OnInit {
    itemID = null;
    item: any = {};
    busy: Promise<any>;
    language: any;
    baseUrl: string =   environment.config.API_URL;
    socketUrl: string =   environment.config.SOCKET_URL;

    constructor(
        public router: Router,
        private activatedRoute: ActivatedRoute,
        private klassService: UserQuestionsService
    ) {  }

    ngOnInit() {
        this.activatedRoute.params.subscribe((params: Params) => {
            this.itemID = params['id'];
            this.getItem();
        });
    }

    getItem() {

        this.busy = this.klassService.getItem(this.itemID, 'userquestions').then((res) => {
            if(!res)
                return true;
            this.item = res.data;
            console.log (this.item);
        });
    }
}


